/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 55px;
  height: 26px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 20px;
}

.slider.round:before {
  border-radius: 50%;
}
:root {
  --background-color: #fff;
  --text-color: #121416d8;
  --link-color: #543fd7;
}

html[data-theme='light'] {
  --background-color: #fff;
  --background-alt-color: #dae0e6;
  --background-accent-color: #f1f3f4;
  --border-color: #161b22;
  --text-color: #121416d8;
  --create-border-color: #c4c4c4;
  --link-color: #543fd7;
  --accent-color: #07AD6D;
  --border-hover: #161B22FF
}

html[data-theme='dark'] {
  --background-color: #0d1117;
  --background-alt-color: #0D1117FF;
  --background-accent-color: #161b22;
  --create-border-color: rgba(255, 255, 255, 0.09);
  --border-color: #f1f3f4;
  --text-color: rgba(247, 248, 248, 0.97);
  --link-color: #828fff;
  --accent-color: #07AD6D;
  --border-hover: rgba(245, 245, 245, 0.49);
}

