.form-control {
    margin-bottom: 10px;
    padding-bottom: 20px;
    position: relative;
}

.form-control label {
    display: inline-block;
    margin-bottom: 5px;
}

.form-control input {
    border: 2px solid whitesmoke;
    border-radius: 4px;
    display: block;
    font-family: inherit;
    width: 93%;
    padding: 10px;
    font-size: 14px;
}

.form-control.success input {
    border-color: forestgreen;
}

.form-control.error input {
    border-color: darkred;
}

.form-control.success i.fa-check-circle {
    color: forestgreen;
    visibility: visible;
}

.form-control.error i.fa-exclamation-circle {
    color: darkred;
    visibility: visible;
}

.form-control i {
    visibility: hidden;
    position: absolute;
    top: 36px;
    right: 10px;
}

.form-control small {
    visibility: hidden;
    position: absolute;
    bottom: 0;
    left: 0;
}

.form-control.error small {
    color: darkred;
    visibility: visible;
}


.form-submit {
