

.post-container {
    border: 1px solid var(--create-border-color);
    border-radius: 3px;
    display: flex;
    margin-top: 25px;
    overflow: hidden;
}

.post-container:hover {
    border-color: var(--border-hover);
}

.post-likes-container {
    min-height: 100%;
    width: 100px;
    display: flex;
    justify-content: center;
    background-color: var(--background-accent-color);
    padding-top: 40px;
}

.post-info {
    padding-left: 10px;
    background-color: var(--background-color);
    display: flex;
    flex-direction: column;
    width: calc(100% - 100px);
}

.post-header h2 {
    margin: 5px 0;
    padding: 0;
    font-size: 18px;
}

.post-text {
    margin: 5px 0;
}

